.stack {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}
p.work {
  font-size: 12px;
}
.stack img {
  width: 50px;
  height: 50px;
  padding: 7.5px;
  filter: drop-shadow(-2px 2px 2px #777);
}
.stack img:hover {
  width: 65px;
  height: 65px;
  padding: 5px;
}
.other-work p {
  font-size: 12px;
  line-height: 12px;
}
.other-work p * {
  vertical-align: middle;
}
@media screen and (max-width: 540px) {
  .projects {
    min-height: 200px;
    margin: 20px 0;
  }
  .old-projects.welcome ul {
    padding-left: 10px;
  }
  .stack img {
    width: 46px;
    height: 46px;
  }
}

/* Retro Download.com / Shareware Style */
.browser-viewport div#work {
  background: linear-gradient(180deg, #e6e6ff 0%, #d6f0ff 100%);
  font-family: 'Verdana', 'Arial', sans-serif;
  color: #000000;
  position: relative;
}

/* Diagonal stripes */
.browser-viewport div#work::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 20px,
    rgba(100, 150, 200, 0.2) 20px,
    rgba(100, 150, 200, 0.2) 40px
  );
  pointer-events: none;
}

.browser-viewport div#work .page-content h1 {
  background: linear-gradient(to right, #003366, #0066cc);
  color: #ffffff;
  padding: 10px;
  font-size: 24px;
  font-family: 'Arial Black', sans-serif;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  border: 2px solid #000033;
  margin-bottom: 20px;
  text-align: center;
}

/* Software listing table style */
.browser-viewport .stack {
  background: #ffffff;
  border: 2px solid #808080;
  padding: 10px;
  margin: 20px 0;
  box-shadow:
    inset -1px -1px 0 #ffffff,
    inset 1px 1px 0 #000000;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 15px;
}

/* Tech stack items as software downloads */
.tech-item {
  background: #f0f0f0;
  border: 1px solid #808080;
  padding: 10px;
  text-align: center;
  position: relative;
  transition: all 0.2s;
}

.tech-item:hover {
  background: #ffffff;
  border-color: #0066cc;
  transform: translateY(-2px);
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.browser-viewport .stack img {
  width: 48px;
  height: 48px;
  padding: 0;
  margin-bottom: 5px;
  filter: none;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.browser-viewport .stack img:hover {
  width: 48px;
  height: 48px;
  padding: 0;
}

/* File info styling */
.file-info {
  font-size: 9px;
  color: #666666;
  margin-top: 5px;
  font-family: 'Courier New', monospace;
}

.file-size {
  color: #008000;
  font-weight: bold;
}

.version {
  color: #0000ff;
}

/* Star rating */
.star-rating {
  color: #ffcc00;
  font-size: 12px;
  margin: 5px 0;
}

/* Tech status */
.tech-status {
  background: linear-gradient(180deg, #00ff00 0%, #00aa00 100%);
  border: 2px inset #00aa00;
  color: white;
  font-weight: bold;
  font-size: 10px;
  padding: 4px 8px;
  margin-top: 5px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  width: 100%;
  text-align: center;
}

/* NEW and UPDATED badges */
.tech-badge {
  position: absolute;
  top: -5px;
  right: -5px;
  background: #ff0000;
  color: #ffffff;
  padding: 2px 6px;
  font-size: 8px;
  font-weight: bold;
  border: 1px solid #000000;
  animation: flash-badge 1s ease-in-out infinite;
}

.tech-badge.updated {
  background: #0066cc;
}

@keyframes flash-badge {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

/* System Requirements Box */
.system-requirements {
  background: #ffffcc;
  border: 2px dashed #cc9900;
  padding: 15px;
  margin: 20px 0;
  font-family: 'Courier New', monospace;
  font-size: 11px;
}

.system-requirements h3 {
  color: #cc0000;
  font-size: 14px;
  margin-bottom: 10px;
  text-decoration: underline;
}

.system-requirements ul {
  list-style-type: none;
  padding-left: 0;
}

.system-requirements li::before {
  content: '✓ ';
  color: #00aa00;
  font-weight: bold;
}

/* Download counter */
.download-counter {
  text-align: center;
  background: #333333;
  color: #00ff00;
  padding: 5px;
  font-family: 'Courier New', monospace;
  font-size: 10px;
  margin: 10px 0;
  border: 1px solid #00ff00;
}

/* Category tabs */
.category-tabs {
  display: flex;
  gap: 0;
  margin-bottom: 20px;
  border-bottom: 2px solid #0066cc;
}

.category-tab {
  background: #e0e0e0;
  border: 1px solid #808080;
  border-bottom: none;
  padding: 5px 15px;
  cursor: pointer;
  font-size: 11px;
  font-weight: bold;
}

.category-tab:hover {
  background: #f0f0f0;
}

.category-tab.active {
  background: #ffffff;
  border-color: #0066cc;
  color: #0066cc;
}

/* Shareware notice */
.shareware-notice {
  background: repeating-linear-gradient(45deg, #ffff00, #ffff00 10px, #ffcc00 10px, #ffcc00 20px);
  border: 2px solid #cc0000;
  padding: 10px;
  text-align: center;
  font-weight: bold;
  color: #cc0000;
  margin: 20px 0;
  animation: pulse-notice 2s ease-in-out infinite;
}

@keyframes pulse-notice {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
}

.browser-viewport div#work .page-content p.work {
  background: #ffffff;
  border: 1px solid #cccccc;
  padding: 10px;
  margin: 10px 0;
  font-size: 11px;
  line-height: 1.5;
  color: #333333;
}

.browser-viewport .other-work {
  background: #f9f9f9;
  border: 2px groove #cccccc;
  padding: 15px;
  margin-top: 20px;
}

.browser-viewport .other-work button {
  background: linear-gradient(to bottom, #0099ff, #0066cc);
  color: #ffffff;
  border: 2px solid #003366;
  padding: 8px 20px;
  font-size: 12px;
  font-weight: bold;
  cursor: pointer;
  text-transform: uppercase;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.browser-viewport .other-work button:hover {
  background: linear-gradient(to bottom, #00aaff, #0077dd);
}

.browser-viewport .other-work button:active {
  box-shadow: inset 2px 2px 5px rgba(0, 0, 0, 0.3);
}
